}

.display__history {
  height: 1.8rem;
  font-size: 1.6rem;
  line-height: 1;
}

.display__main {
  height: 3rem;
  line-height: 1;
}

.keyboard {
  width: 100%;
  background-color: var(--very-dark-d-blue-toggle-background-keypad-background);
  border-radius: .8rem;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
  padding: 2rem;
  display: grid;
}

